import { Component, OnInit} from '@angular/core';
import {FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';

import {ProductService} from '../shared/product.service';
import {Information} from '../recommend/information.component';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css'],
  animations: [
    trigger('btnState', [
      state('inactive', style({
        transform: 'scale(1)'
      })),
      state('active',   style({
        backgroundColor: '#e06366',
        transform: 'scale(1.1)'
      })),
      transition('inactive <=> active', animate('100ms ease-in'))
    ])
  ]
})
export class SearchComponent implements OnInit {
  catetories: string[];
  formModel: FormGroup;
  state = 'inactive';
  infors: Information[];
  btnValue = '搜索';
  private sillsInfor: InforSkill[];

  constructor(private fb: FormBuilder,
              private productService: ProductService) {
    this.formModel = fb.group({
      position: [null,
        [
          Validators.minLength(2),
          Validators.required
        ]
      ],
      comp: [null, Validators.minLength(2)],
      place: [null, Validators.minLength(2)],
      salary: [null, this.positiveNumberValidator],
      time: ['-1'],
      exper: ['0'],
      edu: ['0'],
      skills: this.fb.array([])
    });

    this.infors = [
      new Information('同程旅游', '[苏州·独墅湖]', '15K-25K', '大数据开发', '2018-01-15'),
      new Information('博悦科创', '[上海·张江]', '10k-20k', '大数据开发', '1天前发布'),
      new Information('厚本金融', '[上海·打浦桥]', '25K-40K', '大数据开发经理', '2018-01-19'),
      new Information('江苏亿科达', '[深圳·亮马桥]', '10K-20K', '大数据开发', '09:38发布'),
      new Information('中电进出口', '[成都·中和]', '12K-21K', '大数据开发', '2天前发布'),
      new Information('点我达', '[杭州·东新路]', '30K-50K', '大数据开发', '15:16发布'),
      new Information('有鱼金融科技', '[上海·漕河泾]', '15K-30K', '大数据开发', '1天前发布'),
    ];
    this.sillsInfor = [
      {skillInfor: 'Python'},
      {skillInfor: 'Reyon'},
      {skillInfor: 'wooiie'},
      {skillInfor: 'Rech'},
      {skillInfor: 'Orio'}
    ];
    this.setSkills(this.sillsInfor);
  }



  /*验证输入数值是否为正数*/
  positiveNumberValidator(control: FormControl): any {
    if (!control.value) {
      return null;
    }
    const prices = parseInt( control.value, 10);
    if (prices > 0) {
      return null;
    } else {
      return {positiveNumber: true};
    }
  }

  setSkills(skills: InforSkill[]) {
    const skillsFGs = skills.map(tempt => this.fb.group(tempt));
    const skillFormArray = this.fb.array(skillsFGs);
    this.formModel.setControl('skills', skillFormArray);
  }
  toggleState() {
    this.state = this.state === 'active' ? 'inactive' : 'active';
    this.onSearch();
    this.btnValue = this.btnValue === '搜索' ? '暂停' : '搜索';
  }
  onSearch() {
  }
  onSubmit() {
    if ( this.formModel.valid) {
      console.log(this.formModel.value);
      console.log('OK');
      // this.revert();
    } else {
      console.log(this.formModel.value);
      console.log('error');
    }
  }
  ngOnInit() {
    this.catetories = this.productService.getAllCategories();
  }
  /*恢复表单 - 文字*/
  revert() { this.formModel.reset(); }
}
export class InforSkill {
  skillInfor = '';
}



